    <!--  -->
    <el-dialog :visible.sync="bindSalseAlert">
      <h4><span>{{bindNum}}</span></h4>
      <el-tabs v-model="activeName2" type="card">
        <el-tab-pane label="" name="first">
          <el-form :inline="true" >
            <el-form-item>
              <el-input placeholder="" v-model="noBindSearchContent">
                <el-select  slot="prepend" v-model="noBindSearchType" @change="selectOption">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
                <el-button slot="append" icon="search" @click="noBindSearch()"></el-button>
              </el-input>
            </el-form-item>
            <el-form-item style="font-size:40px;float:right">
              <el-button type="primary" v-on:click="bindAlertSearch()"></el-button>
            </el-form-item>
          </el-form>
          <el-table ref="multipleTable" :data="noBindUserList"  border style="width: 100%" @selection-change="handleSelectionChange1" height="500">
            <el-table-column  type="selection"  width="55">  </el-table-column>
            <el-table-column  prop="trueName"  align="center"  label="">  </el-table-column>
            <el-table-column  prop="phone"  align="center"  label="">  </el-table-column>
            <el-table-column  prop="certification.companyName"  align="center"  label="">  </el-table-column>
            <el-table-column  label=""  align="center" >
              <template scope="scope">
                <el-button type="primary" v-on:click="bindThisUser(scope.row,scope.$index)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <el-form :inline="true" >
            <el-form-item>
              <el-input placeholder="" v-model="BindSearchContent">
                <el-select  slot="prepend" placeholder="" v-model="BindSearchType">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
                <el-button slot="append" icon="search" @click="BindSearch()"></el-button>
              </el-input>
            </el-form-item>
            <el-form-item style="float:right">
              <el-button type="primary" v-on:click="cancleBindAlert()"></el-button>
            </el-form-item>
          </el-form>
          <el-table ref="multipleTable1" :data="bindedUserList"  border style="width: 100%" @selection-change="handleSelectionChange2" height="500">
            <el-table-column  type="selection"  width="55">  </el-table-column>
            <el-table-column  prop="trueName"  align="center"  label="">  </el-table-column>
            <el-table-column  prop="phone"  align="center"  label="">  </el-table-column>
            <el-table-column  prop="certification.companyName"  align="center"  label="">  </el-table-column>
            <el-table-column  label=""  align="center" >
              <template scope="scope">
                <el-button type="primary" v-on:click="cancleBindThisUser(scope.row,scope.$index)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>

    <!--  -->
    <el-dialog custom-class="asgagewgf" title="" :visible.sync="showSaleDetailInfor">
      <div class="personalInfor">
        <img src="" alt="">
        <h3></h3>
        <div>
          <span>{{someOneUserDetails.info.saleId}}</span>
          <span>{{someOneUserDetails.info.trueName}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.info.phone}}</span>
          <span>{{someOneUserDetails.info.sex}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.info.weChar}}</span>
          <span>{{someOneUserDetails.info.email}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.info.birthday}}</span>
          <span>{{someOneUserDetails.info.education}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.info.workUnit}}</span>
          <span>{{someOneUserDetails.info.workPosition}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.info.part}}</span>
          <span>{{someOneUserDetails.info.address}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.info.idCard}}</span>
          <span>{{someOneUserDetails.info.created}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.info.money}}</span>
          <span>{{someOneUserDetails.info.bindUserNum}}</span>
        </div>
      </div>
      <div class="certification">
        <h3></h3>
        <div>
          <span>{{someOneUserDetails.getMoneyStyle.postalType}}</span>
          <span>{{someOneUserDetails.getMoneyStyle.bankName}}</span>
        </div>
        <div>
          <span>{{someOneUserDetails.getMoneyStyle.openName}}</span>
          <span>{{someOneUserDetails.getMoneyStyle.accountNumber}}</span>
        </div>
      </div>
      <h3></h3>
      <el-table :data="someOneUserDetails.bindUsers" border>
        <el-table-column align="center" property="userId" label="" width="150"></el-table-column>
        <el-table-column align="center" property="phone" label=""></el-table-column>
        <el-table-column align="center" property="trueName" label="" width="200"></el-table-column>
        <el-table-column align="center" property="certification.companyName" label=""></el-table-column>
      </el-table>
      <paging :childmsg="d_pageProps" style="text-align:center;margin-top:20px;" @childSay="d_pageHandler"></paging>
    </el-dialog>

    <!--  -->
    <el-table :data="salesList"  border style="width: 100%">
      <el-table-column  prop="trueName"  align="center"  label="">  </el-table-column>
      <el-table-column  prop="phone"  align="center"  label="">  </el-table-column>
      <el-table-column  prop="created"  align="center"  label=""> 
        <template scope="scope">
          <span>{{new Date(scope.row.created).getFullYear()+'-'+ (new Date(scope.row.created).getMonth()+1)+'-'+new Date(scope.row.created).getDate()}}</span>
        </template>
      </el-table-column>
      <el-table-column  prop="isBindUser"  align="center"  label=""> 
        <template scope="scope">
          <span v-if="scope.row.isBindUser === 1"></span>
          <span v-else-if="scope.row.isBindUser === 2"></span>
        </template>
      </el-table-column>
      <el-table-column  prop="bindUserNum"  align="center"  label="">  </el-table-column>
      <el-table-column  label=""  align="center">
        <template scope="scope">
            <el-button v-if="scope.row.isBindUser=='2'" type="text"  v-on:click="bindUser(scope.$index, scope.row)"></el-button>
            <el-button v-else type="text"  v-on:click="cancleBindUser(scope.$index, scope.row)"></el-button>
            <el-button type="text"   v-on:click="saleDetail(scope.$index,scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <paging :childmsg="pageProps" style="text-align:center;margin-top:20px;" @childSay="pageHandler"></paging>
  </el-row>
</template>
<script>
  import global from '../../global/global'
  import paging from '../../website/brandLib/paging0'
  export default{
    data(){
      return {
        noBindSearchType:"",
        BindSearchType:"",
        pageProps: {
          pageNum: 1,
          totalPage: 1
        },
        d_pageProps: {
          pageNum: 1,
          totalPage: 1
        },
        noBindSearchContent:null,
        BindSearchContent:null,
        multipleSelection1: [],
        multipleSelection2: [],
        activeName2: 'first',
        showSaleDetailInfor:false,
        bindSalseAlert:false,
        searchUserContent:'',
        searchUserType:"",
        searchState:"",
        bindNum: 0,
        salePhone: '',
        bindedUserList:[],
        noBindUserList:[],
        someOneUserDetails:{
          info:{
            bianhao:"123134",
            name:"23545",
            phone:"1234",
            sex:'aef',
            ID:"134141341431341414",
            time:"1234-12-12",
            leaveMoney:'2112',
            bindUserNum:123,
            saleImg:"1.png"
          },
          getMoneyStyle:{
            type:"a",
            count:1231313123
          },
          bindUsers:[
            {bianhao:'12',name:'w4er',phone:'32424',componey:'2w323'},
            {bianhao:'12',name:'w4er',phone:'32424',componey:'2w323'},
            {bianhao:'12',name:'w4er',phone:'32424',componey:'2w323'},
            {bianhao:'12',name:'w4er',phone:'32424',componey:'2w323'},
          ]

        },
        salesList:[
          {saleId:12121,name:'asdfaf&&1234141414',phone:31414141414143,time:"asdfa",bindUser:"",userNum:""},
          {saleId:12121,name:'asdfaf&&1234141414',phone:31414141414143,time:"asdfa",bindUser:"",userNum:12},
          {saleId:12121,name:'asdfaf&&1234141414',phone:31414141414143,time:"asdfa",bindUser:"",userNum:""},
          {saleId:12121,name:'asdfaf&&1234141414',phone:31414141414143,time:"asdfa",bindUser:"",userNum:12},
          {saleId:12121,name:'asdfaf&&1234141414',phone:31414141414143,time:"asdfa",bindUser:"",userNum:""},
          {saleId:12121,name:'asdfaf&&1234141414',phone:31414141414143,time:"asdfa",bindUser:"",userNum:12}
        ],
        userTypes: [
          {value: '',label: ''},
          {value: '',label: ''},
          {value: '',label: ''}
        ],
        states:[
          {value: '',label: ''},
          {value: '1',label: ''},
          {value: '2',label: ''}       
        ],
      }
    },
    components: {
      paging
    },
    watch:{
      multipleSelection1:{
        handler:function(){
          var that = this;
          console.log(that.multipleSelection1)
        },
        deep:true
      }
    },
    created(){
      this.queryHandler()
    },
    methods: {
      selectOpt(key){
        this.searchUserType = key;
        this.searchUserContent = '';
      },
      selectOption(){
        this.noBindSearchType = key;
        this.noBindSearchContent = '';
      },
      queryHandler: function(){
        var params;
        if(this.searchUserType === ''){
          params = {
            saleId: '',
            phone: this.searchUserContent,
            trueName: '',
            isBindUser: this.searchState,
            currentPage: this.pageProps.pageNum,
            numberPerPage: 10,
            token: global.getToken()
          }
        }else if(this.searchUserType === ''){
          params = {
            saleId: '',
            phone: '',
            trueName: this.searchUserContent,
            isBindUser: this.searchState,
            currentPage: this.pageProps.pageNum,
            numberPerPage: 10,
            token: global.getToken()
          }
        }
        console.log('',params)
        global.axiosGetReq('/saleList/query',params).then((res) => {
          if(res.data.callStatus === 'SUCCEED'){
            console.log(res.data.data)
            this.salesList = res.data.data
          }
        })
      },
      pageHandler(data){
        this.pageProps.pageNum = data
        this.queryHandler();
      },
      d_pageHandler(data){
        this.d_pageProps.pageNum = data
        // this.saleDetail(index, row) 
      },
      BindSearch:function(){
        //
        let params;
        if(this.BindSearchType === ""){
          params = {
            phone: this.BindSearchContent,
            trueName: '',
            companyName: '',
            isBind: 2
          }
        }else if(this.BindSearchType === ""){
          params = {
            phone: '',
            trueName: this.BindSearchContent,
            companyName: '',
            isBind: 2
          }
        }else if(this.BindSearchType === ""){
          params = {
            phone: '',
            trueName: '',
            companyName: this.BindSearchContent,
            isBind: 2
          }
        }
        console.log('',params)
        global.axiosGetReq('/saleList/userlist',params).then((res) => {
          if(res.data.callStatus === 'SUCCEED'){
            this.bindedUserList = res.data.data
            console.log(res.data.data)
          }
        })
        this.BindSearchContent = null;//
        
      },
      noBindSearch:function(){
        //
        let params;
        if(this.noBindSearchType === ""){
          params = {
            phone: this.noBindSearchContent,
            trueName: '',
            companyName: '',
            isBind: 1
          }
        }else if(this.noBindSearchType === ""){
          params = {
            phone: '',
            trueName: this.noBindSearchContent,
            companyName: '',
            isBind: 1
          }
        }else if(this.noBindSearchType === ""){
          params = {
            phone: '',
            trueName: '',
            companyName: this.noBindSearchContent,
            isBind: 1
          }
        }
        console.log('',params)
        global.axiosGetReq('/saleList/userlist',params).then((res) => {
          if(res.data.callStatus === 'SUCCEED'){
            this.noBindUserList = res.data.data
            console.log(res.data.data)
          }
        })
        this.noBindSearchContent = null;//
      },
      // headSearch:function(){
      //   var that = this;
      //   var searchObj = {
      //     searchUserType:that.searchUserType,
      //     searchState:that.searchState
      //   };
      //   if(that.searchUserContent){
      //     searchObj.searchUserContent = that.searchUserContent;


      //     that.searchUserContent = null;//
      //   }else{
      //     this.$alert("", {confirmButtonText: ''});
      //   }
      // },
      bindAlertSearch:function(){
        var that = this;
        if(that.multipleSelection1.length==0){
          this.$alert("", {confirmButtonText: ''});
        }else{

        }
      },
      cancleBindAlert:function(){
        var that = this;
        if(that.multipleSelection2.length==0){
          this.$alert("", {confirmButtonText: ''});
        }else{

        }
      },
      bindThisUser:function(nowUser,index){
        //
        var that = this;
        var obj = {};
        if(nowUser==that.multipleSelection1[0]){
          obj = {
            salePhone: this.salePhone,
            userPhone: nowUser.phone
          }
          global.axiosPostReq('/saleList/bind',obj).then((res) => {
            if(res.data.callStatus === 'SUCCEED'){
              this.$message({
                type: 'success',
                message: '!'
              });
              this.noBindSearch()

            }
          })
        }else{
          this.$alert("", {confirmButtonText: ''});
        }
      },
      cancleBindThisUser:function(nowUser,index){
        //
        var that = this;
        var obj = {};
        if(nowUser==that.multipleSelection2[0]){
          obj = {
            salePhone: this.salePhone,
            userPhone: nowUser.phone
          }
          global.axiosPostReq('/saleList/disBind',obj).then((res) => {
            if(res.data.callStatus === 'SUCCEED'){
              this.$message({
                type: 'success',
                message: '!'
              });
              this.BindSearch()
            }
          })
        }else{
          this.$alert("", {confirmButtonText: ''});
        }
      },
      handleSelectionChange1:function(val) {
        this.multipleSelection1 = val;
      },
      handleSelectionChange2:function(val) {
        this.multipleSelection2 = val;
      },
      bindUser:function(index, row){
        this.bindSalseAlert = true;
        this.activeName2 = "first";
        this.bindNum = 0;
        this.salePhone = row.phone;
        this.noBindSearch();
      },
      cancleBindUser:function(index, row){
        this.bindSalseAlert = true;
        this.activeName2 = "second";
        this.bindNum = row.bindUserNum;
        this.salePhone = row.phone;
        this.BindSearch();
      },
      saleDetail:function(index, row){
        //
        this.showSaleDetailInfor = true;
        let params = {
          phone: row.phone,
          currentPage: this.d_pageProps.pageNum,
          numberPerPage: 10,
          token: global.getToken()
        }
        console.log('',params)
        global.axiosGetReq('/saleList/detail',params).then((res) => {
          if(res.data.callStatus === 'SUCCEED'){
            this.someOneUserDetails.info = res.data.data
            this.someOneUserDetails.bindUsers = res.data.data.user
            console.log(res.data.data)
          }
        })


      },
      search:function(){},

    },
  }
</script>

<style>
  .saleListWarp .el-select .el-input{
    width: 120px;
  }
    .saleListWarp h4{
      line-height: 50px;
    }
  /*.saleListWarp .el-input__inner{
    width: 200px;
  }*/
  .saleListWarp   .asgagewgf h3{
      font-weight: 500;
      line-height: 50px;
      color: #20a0ff;
    }
  .saleListWarp   .asgagewgf .personalInfor{
      position: relative;
    }
  .saleListWarp   .asgagewgf .personalInfor img{
      position: absolute;
      top: 0;
      right: 0;
      width: 150px;
      height: 150px;
    }
  .saleListWarp   .asgagewgf  span{
      width: 40%;
      display: inline-block;
      line-height: 35px;
    }
  .t_input_w{
    width:320px!important;
  }
  .t_select_width{
    width:120px;
  }

</style>



